import './sass/main.scss';
import '@pnotify/core/dist/Material.css';
import { defaults } from '@pnotify/core';
defaults.styling = 'material';
defaults.icons = 'material';
import { error } from '@pnotify/core';

const countryInputRef = document.querySelector('.search-form__input');
countryInputRef.addEventListener('input', createData);

const getRestData = function (countryName) {
  return fetch(`https://restcountries.eu/rest/v2/name/${countryName}`, {
    method: 'GET',
    headers: {
      Accept: 'application/json',
    },
  }).then(response => {
    if (response.ok) return response.json();
    throw new Error('Error fetching data');
  });
};

function createData(event) {
  const countryName = event.target.value;
  if (countryName.length === 0) return;
  getRestData(countryName)
    .then(data => {
      if (data.length > 10) {
        // const stackMy = new Stack({
        //   dir1: 'down',
        //   dir2: 'left',
        //   firstpos1: 25,
        //   firstpos2: 25,
        //   spacing1: 36,
        //   spacing2: 36,
        //   push: 'bottom',
        //   context: document.body,
        // });
        error({
          text: 'To many matches found. Please enter more specific query',
          //   stack: stackMy,
        });
      }
    })
    .catch(err => {
      error({
        text: `${err}`,
      });
    });
}
